.editContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.optionHolder{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}

.contentHolder{
    width: 100%;
    display: flex;
    flex-direction: column;
}


@media only screen and (min-width: 800px) {
    .editContainer{
        height: 100%;
    }
    .contentHolder{
        height: 100%;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        padding: 0 10%;
    }
}

.topBar {
    width: 80%;
    padding: 20px 0px;
}
.topBar svg{
    stroke: black;
    width: 20px;
    height: 20px;
    cursor: pointer;
}

.topBar svg:first-of-type{
    float:left;
}

.topBar svg:nth-of-type(2){
    float:right;
}

.clearFix{
    display: table;
    clear: both;
}

.image{
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}
.image img {
    width: 100%;

}


.modeSetting{
    margin: 10px 0px;
}

.Options{
    width: 80%;
    margin-top: 20px;
}

.Option{
    margin-top:20px;
    display: flex;
    flex-direction: row;
}

.Option div{
    flex-grow: 1;
}

.optionaLabel {
    float:right;
    font-size: 16px;
}

.optionGear div{
    float:left;
}


.colorPickerContainer{
    margin: 20px 0px 50px 0px;
}

.colorPicker{
    width: 60%;
    margin: auto;
}

.gallery{
    margin: 0px 24px;
}

.buttonHolder{
    width: 100%;
    margin-top: 40px;
}

.imageLoading{
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
}

.imageLoading img{
    stroke: greenyellow;
    stroke-width: 1px;
    width: 60px;
    height: 60px;
    -webkit-animation:spin 1s linear infinite;
    -moz-animation:spin 1s linear infinite;
    animation:spin 1s linear infinite;
}

@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

.displayNone {
    display: none;
}