.button{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #EFFBEF;
    border-radius: 30px;
    width: 30%;
    cursor: pointer;
}
.button .content{
    width: 100%;
    padding: 0% 0px 10% 0px;
}

.button .content .icon{
    text-align: center;
    height: 100px;
}

.button .content .icon svg{
    width: 30%;
    height: auto;
    stroke-width: 1px;
    stroke: #7bb372;
}

.button:first-child{
    margin-left: 10%;
}

.button .content p{
  text-align: center;
  color: #7bb372;
  font-size: 17px;
}