.galleryRow{
    display: flex;
    flex-direction: row;
    max-width: 100%;
    justify-content: center;
    margin-top: 20px;
    align-items: center;
}
.image{
    border-radius: 20px;
    max-width: 100%;
    flex: 1;
}
.holder{
    cursor: pointer;
    margin: 8px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.selected .cover {
    display: flex;
}

.cover{
    background-color: rgba(195, 229, 190, 0.5);
    width: 100%;
    height: 100%;
    border-radius: 20px;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    display: none;
}

.cover svg{
    stroke: white;
    stroke-width: 1px;
    width: 55px;
    height: 55px;
}