.buttons{
    display: flex;
    flex-direction: row-reverse;
}

.buttons button:first-of-type {
    border-radius: 5px 0px 0px 5px;
}

.buttons button:last-of-type {
    border-radius: 0px 5px 5px 0px;
    direction: ltr;
}

/* .buttons button:first-of-type .normal .name {
    margin-right: 5px;
} */

.buttons button{
    cursor: pointer;
    align-items: center;
    justify-content: center;
    display: flex;
    border: 0px;
    background-color: #e5e5e5;
}

.normal{
    width: 80px;
}

.normal .name {
    color: #5c5c5c;
    font-family: dana;
    display: inline-block;
    height: 25px;
    line-height: 26px;
    font-size: 10px;
    margin-left: 5px;
}

.normal .name:first-of-type {
    margin-right: 5px;
}

.tiny {
    padding: 5px 7px;
}

.tiny .name {
    color: #000000;
    font-family: dana;
    display: inline-block;
    height: 15px;
    line-height: 19px;
    font-size: 10px;
}

.icon svg{
    width: 15px;
    height: 15px;
    vertical-align: middle;
    stroke:  #5c5c5c;
}

button:focus{
    border: none;
    outline: none;  
}

.buttons .buttonSelected{
    background-color: #c3e5be;
    border: none;
    outline: none;
}