@font-face {
  font-family: dana;
  src: url('./dana-regular.woff');
}

body{
  direction: rtl;
  margin: 0px;
  font-family: dana;
  height: 100%;
}

html{  
  height: 100%;
}

*{
  box-sizing: border-box;
  -moz-user-select: none;
  user-select: none;
  -webkit-user-select: none;
  -webkit-user-drag: none;
}

#root{
  height: 100%;
}