.App{
  display: flex;
  flex-direction: column;
}
.bigButtons{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 50%;
}

.recently{
  width: 80%;
  margin: auto;
}

.title{
  font-family: dana;
  margin-right:10%;
  margin-top: 24px;
}

p.intro {
  font-style: italic;
  font-size: 13px;
  padding: 0px 10%;
  color: gray;
  font-family: 'dana';
  width: 70%;
}

h1.intro{
  color: #7bb372;
  margin: 24px 10%;
  font-family: dana;  
}

.actionsHolder{
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 800px) {

  .bigButtons{
    width: 100%;
  }
  p.intro {
    width: 100%;
  }
}